
$table-header-color:rgb(14,122,254);
$submit-button-color:rgb(19, 152, 19);
$close-button-color: 	#800000;

.team-member-container{
height: 100%;
// display:flex

}

.pageHeading{

    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1.5px;
    padding: 10px;
    float:left;
    transition: letter-spacing 0.5s;
    transition-timing-function: ease-in;

    &:hover{
        letter-spacing: 20px;

    }
    

}
nav{
    background-color: antiquewhite;
    overflow: hidden;
    vertical-align: center;
    
}

.modal{
    position: fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content{
    width:50%;
    min-width: 700px;
    height:auto;
    background-color: white;
    overflow: auto;

}

.modal-header {
    padding: 10px;
    max-height: 30px;
    background-color: wheat;
}

.modal-footer{
    padding: 10px;
    max-height: 30px;
    
}

.modal-title{
    margin:0;
    letter-spacing: 1px;

}

.modal-body{
    padding: 10px;
    border-top:1px solid #eee;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: center;
    min-height: 50vh;
    align-items: center;
}
.form-input-container{
    
}
.form-label{
    display: block;
    padding:10px;
    font-size: 20px;
   
    // align-items: center;

}
.form-input{
    margin-left:10px;
    padding: 5px;
    border:none;
    border-bottom: 4px solid $table-header-color;
    background-color: lightblue;
    // cursor: auto;

    &:focus{
        background-color: lightblue;
       
        outline: none;
        
    }
}
form {
    border:1px sloid black;
    width: 80%;
    height:80%;
    justify-content: center;
}

.button-basic{
    padding:5px;
    border-radius: 10px;
    // border-color: aquamarine;
    width:80px;
    font-size: large;
    text-align: center;
    font-weight: bold;
}
.small-button{
    padding:2px;
    width:25px;
    font-size: medium;
    text-align:center;
    font-weight: bold;
    margin:10px;
    border-radius: 5px;

}
.delete-button{
    @extend .small-button;
    background-color: #800000;
    color:wheat;
}

.add-button{
    @extend .button-basic;
    margin:5px;
    float: right;
    background-color: $table-header-color;
    color: wheat;
  
}
.submit-button{
    @extend .button-basic;
    margin:5px;
    float:right;
    background-color: green;
    color: wheat;
}

.close-button{
    @extend .submit-button;
    background-color: $close-button-color;

}

form{
    display: block;
    color:blue;
}

table{
  
    align-self: center;
    width:90%;
    margin:5%;
    justify-self: center;
    border-color: aquamarine;
    border-collapse: collapse;
    // font-size: 50%;
    padding: 10px;
    border:1px solid gr;

 

    thead {
        background-color:$table-header-color;
        color:whitesmoke;
        z-index: 9999;
        
    }
    tbody{
        background-color: wheat;
    

        tr{
            &:hover{
                font-weight:bold;
                color:blue;
                // background-color: lightgrey;
        
            }
            }
    }
    th{
    padding: 5px 10px 5px 10px;
    letter-spacing: 1px;
    }
    
   
    
    td{
        margin:40px;
        padding: 5px;
        letter-spacing: 0.8px;
        vertical-align: center;
        text-align: center;
        text-transform: uppercase;
        font-size:100%;
        font-weight: 550;
        border-bottom: 1px solid gray;
        
    }
    
}
